import React, { useState } from 'react';
import { AutoComplete, Button, Typography } from 'antd';

import './EperHeader.css'

const { Text } = Typography;

/**
 * Prepare Label with bold elements
 */
function BoldString({text, boldText}) {
  if (text) {
    let stringMap = [];
    const indices = [...text.matchAll(new RegExp(boldText, 'gi'))].map(a => a.index);
    for (let i = 0; i < indices.length; i++) {
      if (i === 0) {
        stringMap.push({b: false, val: text.slice(0, indices[i])})
      } else {
        stringMap.push({b: false, val: text.slice(indices[i-1] + boldText.length, indices[i])})
      }
      stringMap.push({b: true, val: text.slice(indices[i], indices[i]+boldText.length)})
    }
    stringMap.push({b: false, val: text.slice(indices[indices.length - 1] + boldText.length)})

    return (
      <div>
        {stringMap.map(({b, val}) => {
          return(
            <Text key={Math.random()} strong={b}>{val}</Text>
          )
        })}
      </div>
    )
  } else {
    return;
  }
}

function EperHeader() {
  const [options, setOptions] = useState([]);
  const [query, setQuery] = useState('');
  const [autocompleteOpen, setAutocompleteOpen] = useState(false)


  const renderItem = (id, mTitle, mQuery) => {
    return ({
      value: id,
      label: (
        <BoldString text={mTitle} boldText={mQuery} />
      )
    })
  }

  const test = [
    renderItem("1", 'AutoComplete, Button, Typography', query)
  ]

  return (
    <div className='container'>
      <div className='row'>
        <div className='title'>Eper</div>
        <div className='searchContainer'>
          <AutoComplete
          allowClear
            style={{
              width: 420
            }}
            options={test}
            placeholder='nájsť rastlinu podľa názvu'
            value={query}
            onSearch={(text) => {
              setQuery(text);
              setAutocompleteOpen(text.length > 2)
            }}
            onSelect={(item) => {
              setQuery('')
              setAutocompleteOpen(false)
            }}
            open={autocompleteOpen}
            onBlur={() => {
              setAutocompleteOpen(false)
            }}
            onFocus={() => {
              setAutocompleteOpen(query.length > 2)
            }}
          />
          <Button>hľadať</Button>
        </div>
      </div>
      <div className='delimiter'/>
    </div>
  );
}

export default EperHeader;