/**
 * Create row in filter
 * @param {Object} props
 * @param {string} props.title
 * @param {((e: CheckboxChangeEvent) => void) | undefined} props.onChange 
 */
function FilterRow({title, onChange}) {
  return(
    <div
      style={{
        fontFamily: 'Inter',
        color: '#696969',
        fontSize: 12,
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        height: 30
      }}
    >
      <Checkbox
        onChange={onChange}
        style={{
          marginRight: 12
        }}
      />
      {title}
    </div>
  );
}

/**
 * Filter row for height
 * @param {Object} props
 * @param {number} props.minHeight
 * @param {number} props.maxHeight
 * @returns 
 */
function HeightRow({minHeight, maxHeight}) {
  return(
    <div style={{
      display: 'flex',
      flexDirection: 'row',
    }}>
      min.
      <InputNumber style={{width: 60, marginLeft: 3, marginRight: 3}} size='small' min={0} max={maxHeight | 200} step={10}/>
      cm
      <div style={{flex: 1}}/>
      max.
      <InputNumber style={{width: 60, marginLeft: 3, marginRight: 3}} size='small' min={minHeight | 0} max={200} step={10}/>
      cm
    </div>
  );
}

/**
 * Create base filter cathegory
 * @param {FilterModel} filterModel
 * @param {((index: number, state: boolean) => void) | undefined} onChange 
 * @param {string | number | undefined} key
 */
const renderFilterBaseCategory = (filterModel, onChange, key) => 
  <Collapse.Panel
    style={{fontFamily: 'Inter', fontSize: 14}}
    header={filterModel.title}
    key={key === undefined ? filterModel.title : key}
  >
    {
      (filterModel.filterType === 'basic') ? 
      filterModel.entries.map((item, index) => 
        <FilterRow
          key={index}
          title={item.title}
          onChange={(e) => {
            onChange(index, e.target.checked);
          }}
        />
      ) : (
        <HeightRow />
      )
    }
  </Collapse.Panel>;

export function FilterMenu() {
  return (
    <Collapse
      style={{margin: 30}}
      expandIconPosition='end'
      expandIcon={({ isActive }) => <CaretDownOutlined rotate={isActive ? 180 : 0} />}
    >
    {
      filterData.map((filterRow) => 
        renderFilterBaseCategory(filterRow, (selectedIndex, state) => {
          console.log(filterRow.entries[selectedIndex].title + ' ' + state);
        })
      )
    }
    </Collapse>
  );
}
