import React, { useState } from 'react';
import { Typography, Checkbox } from 'antd';
import { COLORS } from '../common/colors';

const { Text } = Typography;

const handleSpace = (text) => text !== '' ? text + '\xa0' : '';

function CircleElement({color, text, small}) {
  return(
    <div style={{
      border: color ? '1px solid #D9D9D9' : '',
      width: 22,
      height: 22,
      backgroundColor: color,
      borderRadius: 12,
      fontSize: small ? 8 : 10,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      marginLeft: 3,
      color: color === COLORS.flower.burgundy ? '#fff' : '#696969'
    }}>
      {text}
    </div>
  )
}

const colorPerMonth = (color, state) => state !== 0 ? color : null 

function TableItem({item}) {
  return (
  <div 
    style={{
      marginLeft: 30,
      marginRight: 120,
      display: 'flex',
      flexDirection: 'row',
      fontSize: 12,
      alignItems: 'center',
      height: 30
    }}
  >
    <Checkbox
      style={{
        marginRight: 10
      }}
    />

    <div
      style={{
        marginRight: 120,
        display: 'flex',
        flexDirection: 'row',
        fontSize: 12,
        alignItems: 'center',
        flex: 1
      }}
    >
      <i>{handleSpace(item.genus)}</i>
      <i>{handleSpace(item.species)}</i>
      <i>{handleSpace(item.hybrid)}</i>
      <i>{handleSpace(item.subspecies)}</i>
      <b>{item.cultivar}</b>
    </div>

    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        width: 380
      }}
    >
      <CircleElement color={item.color} text='kvet' small/>
      <CircleElement color={item.foliage} text='list' small/>

      <CircleElement color={colorPerMonth(item.color, item.months.I)} text='I.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.II)} text='II.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.III)} text='III.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.IV)} text='VI.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.V)} text='V.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.VI)} text='VI.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.VII)} text='VII.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.VIII)} text='VIII.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.IX)} text='IX.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.X)} text='X.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.XI)} text='XI.'/>
      <CircleElement color={colorPerMonth(item.color, item.months.XII)} text='XII.'/>
    </div>

    <div 
      style={{
        fontSize: 10, width: 50, textAlign: 'right'
      }}
    >{item.height} cm</div>
  </div>
  )
}

export default TableItem;
