const COLORS = {
  flower: {
    white: '#ffffff',
    blue: '#c9daf8',
    green: '#d9ead3',
    yellow: '#ffe599',
    orange: '#f9cb9c',
    pink: '#ead1dc',
    purple: '#d5a6bd',
    violet: '#b4a7d6',
    burgundy: '#a64d79'
  },
  foliage: {
    lightGreen: '#b6d7a8',
    darkGreen: '#6aa84f',
    grayGreen: '#d0e0e3',
    silverGreen: '#c9daf8',
    burgundy: '#a64d79'
  }
}

function colorToHex(color) {
  switch(color) {
    case 'biela':
      return COLORS.flower.white;
    case 'modrá':
      return COLORS.flower.blue;
    case 'zelená':
      return COLORS.flower.green;
    case 'žltá':
      return COLORS.flower.yellow;
    case 'oranžová':
      return COLORS.flower.orange;
    case 'ružová':
      return COLORS.flower.pink;
    case 'purpurová':
      return COLORS.flower.purple;
    case 'fialová':
      return COLORS.flower.violet;
    case 'bordová':
      return COLORS.flower.burgundy;
    case 'svetlozelený list':
      return COLORS.foliage.lightGreen;
    case 'tmavozelený list':
      return COLORS.foliage.darkGreen;
    case 'sivozelelený list':
      return COLORS.foliage.grayGreen;
    case 'strieborný list':
      return COLORS.foliage.silverGreen;
    case 'bordový list':
      return COLORS.foliage.burgundy;
    default:
      return null
  }
}

module.exports = { COLORS, colorToHex }
